<template>
  <div id="app">
    <!--  -->
    <div style="width: 99vw;height: 100vh;background: linear-gradient(180deg, rgba(20, 81, 82, 1) 0%, rgba(218, 231, 182, 1) 100%);">
      <!-- viewsviews 100vh-->
      <HomeView></HomeView>
    </div>
    <div style="display: flex;">
      <!--  -->
      <div>
        <!-- <div class="nav">
          <p class="nav-title">{{ title[current] }}</p>
          <t-divider></t-divider>
          <t-anchor container="#content-view" @click="handleClick">
            <t-anchor-item href="#plant" title=""></t-anchor-item>
            <t-anchor-item href="#pick" title=""></t-anchor-item>
            <t-anchor-item href="#produce" title=""></t-anchor-item>
            <t-anchor-item href="#sale" title=""></t-anchor-item>
          <t-anchor-item href="#taste" title=""></t-anchor-item>
          </t-anchor>
        </div> -->

        <div class="nav">
          <p class="nav-title">{{ title[current] }}</p>
          <t-divider></t-divider>
          <template>
            <t-steps :defaultCurrent="current"
                      separator="dashed"
                      layout="vertical"
                      theme="dot"
                      @change="onChangeSteps">
              <t-step-item title="" content="" />
              <t-step-item title="" content="" />
              <t-step-item title="" content="" />
              <t-step-item title="" content="" />
              <t-step-item title="" content="" />
            </t-steps>
          </template>
        </div>
        <div>
          <!-- <TeaInfoView :data="customEventData"></TeaInfoView> -->
        </div>
      </div>

      <!--  -->
      <div class="content-view" id="content-view" style="overflow-x: hidden;" >
        <section id="plant" class="section-first-3" style="position: relative;">
          <!-- viewsviewscalc100vw - 300px -->
          <!-- <PlantView @customEvent="handleCustomEvent"></PlantView> -->
          <PlantView></PlantView>
        </section>
        <section id="pick" class="section-first-3">
          <!-- viewsviewscalc100vw - 300px -->
          <PickView></PickView>
        </section>
        <section id="produce" class="section-first-3">
          <!-- viewsviewscalc100vw - 300px -->
          <ProduceView></ProduceView>
        </section>
        <section id="sale" class="section-first-3">
          <!-- viewsviewscalc100vw - 300px -->
          <SaleView></SaleView>
        </section>
        <section id="taste" class="section4">
          <!-- viewsviewscalc100vw - 300px -->
          <TasteView></TasteView>
        </section>
      </div>
    </div>
    <div style="width: 100vw">
      <AboutView></AboutView>
    </div>

  </div>
</template>

<script>
import HomeView from './views/HomeView.vue';
import PickView from './views/PickView.vue';
import PlantView from './views/PlantView.vue';
import ProduceView from './views/ProduceView.vue';
import SaleView from './views/SaleView.vue';
import TasteView from './views/TasteView.vue';
import TeaInfoView from './views/TeaInfoView.vue';
import AboutView from './views/AboutView.vue';

  export default {
    components:{
        HomeView,
        PlantView,
        PickView,
        SaleView,
        TasteView,
        TeaInfoView,
        ProduceView,
        AboutView,
    },
    data() {
      return {
        current:0,
        title:['','','','',''],
        scrollHeight:0, //
        metaHeight:15,  //
        metaTime: 10,   //
        targetHeight:0,     //
        clientHeight:0,
        customEventData: -1,
      };
    },
    mounted() {

    },
    beforeDestroy() {

    },
    methods: {
      // handleClick({ e, href, title }) {
      //   e.preventDefault();
      //   console.log('click', href, title);
      // },
      onChangeSteps(e){
        this.current=e;
        this.toArea(e);
      },
      handleScroll(e){
        // console.log(e)
        var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        this.scrollHeight = scrollTop
        let clientHeight = window.innerHeight;
        this.clientHeight = clientHeight
        // console.log(scrollTop,clientHeight);
        if(scrollTop >= clientHeight-300 && scrollTop < 2*clientHeight-300){
          this.current=0;
        }else if(scrollTop >= 2*clientHeight-300 && scrollTop < 3*clientHeight-300){
          this.current=1
        }else if(scrollTop >= 3*clientHeight-300 && scrollTop < 4*clientHeight-300){
          this.current=2;
        }else if(scrollTop >= 4*clientHeight-300 && scrollTop < 5*clientHeight-300){
          this.current=3;
        }else if(scrollTop >= 5*clientHeight-300){
          this.current=4;
        }
      },
      toArea(index) { // index
        switch (index) { // 
          case 0: //
            this.targetHeight= this.clientHeight  //
            break;
          case 1: //
            this.targetHeight= 2*this.clientHeight
            break;
          case 2: //
            this.targetHeight= 3*this.clientHeight
            break;
          case 3: //
            this.targetHeight= 4*this.clientHeight
            break;
          case 4: //
            this.targetHeight= 5*this.clientHeight
            break;
          default: //
            this.targetHeight= this.clientHeight
            break;
        }

        // 
        if(this.targetHeight> this.scrollHeight){
          // 
          let x = this.targetHeight- this.scrollHeight;
          // 
          document.documentElement.scrollTop += x%this.metaHeight;
          x -= x%this.metaHeight;
          const goto = setInterval(() => { // 
            document.documentElement.scrollTop  += this.metaHeight; // 
            x-= this.metaHeight; // 
            if (x == 0) { // 
              clearInterval(goto); //
            }
          }, this.metaTime);
        }
        // 
        else{
          // 
          let x = this.scrollHeight - this.targetHeight;
          // 
          document.documentElement.scrollTop -= x%this.metaHeight;
          x -= x%this.metaHeight;
          const goto = setInterval(() => {
            document.documentElement.scrollTop -= this.metaHeight;
            x-= this.metaHeight;
            if (x == 0) {
              clearInterval(goto); //
            }
          }, 1);
        }
      },
      handleCustomEvent(data){
        this.customEventData = data
        console.log(data);
      }
    },
    created(){
      window.addEventListener('scroll', this.handleScroll);
      let clientHeight = window.innerHeight;
      this.clientHeight = clientHeight
    }

  };
</script>

