          type: 'cubic-horizontal',
          style: {
            stroke: '#32846e80', // 
            lineWidth: 2, // 
          },
          stateStyles: {
            hover: {
              stroke: '#32846e80',
              lineWidth: 4,
            },
          },
        },

      });
      graph.data(data);
      graph.render();

      graph.on('edge:mouseenter', (evt) => {
        const { item } = evt;
        graph.setItemState(item, 'active', true);
      });

      graph.on('edge:mouseleave', (evt) => {
        const { item } = evt;
        graph.setItemState(item, 'active', false);
      });

      graph.on('edge:click', (evt) => {
        const { item } = evt;
        graph.setItemState(item, 'selected', true);
      });
      graph.on('canvas:click', (evt) => {
        graph.getEdges().forEach((edge) => {
          graph.clearItemStates(edge);
        });
      });
    },
    showGraph(graphNumber) {
      // 
      document.getElementById('container').innerHTML = '';
      switch (graphNumber) {
        case 1:
          this.initGraph1();
          this.current=0,
          this.showImage1 = true;
          this.showImage2 = false;
          this.showImage3 = false;
          this.showImage4 = false;
          this.showImage5 = false;
          this.showImage6 = false;
          break;
        case 2:
          this.initGraph2();
          this.current=0,
          this.showImage1 = false;
          this.showImage2 = true;
          this.showImage3 = false;
          this.showImage4 = false;
          this.showImage5 = false;
          this.showImage6 = false;
          break;
        case 3:
          this.initGraph3();
          this.current=0,
          this.showImage1 = false;
          this.showImage2 = false;
          this.showImage3 = true;
          this.showImage4 = false;
          this.showImage5 = false;
          this.showImage6 = false;
          break;
        case 4:
          this.initGraph4();
          this.current=0,
          this.showImage1 = false;
          this.showImage2 = false;
          this.showImage3 = false;
          this.showImage4 = true;
          this.showImage5 = false;
          this.showImage6 = false;
          break;
        case 5:
          this.initGraph5();
          this.current=0,
          this.showImage1 = false;
          this.showImage2 = false;
          this.showImage3 = false;
          this.showImage4 = false;
          this.showImage5 = true;
          this.showImage6 = false;
          break;
        case 6:
          this.initGraph6();
          this.current=0,
          this.showImage1 = false;
          this.showImage2 = false;
          this.showImage3 = false;
          this.showImage4 = false;
          this.showImage5 = false;
          this.showImage6 = true;
          break;
        default:
          // 
          break;
      }
    }
  },

}

</script>

